html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1210px;
  margin: 0 auto;
  padding-right: 25px;
  padding-left: 25px;
}

.container--coaches {
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 1199px) {
  .container {
    max-width: 100%;
    padding-right: 45px;
    padding-left: 45px;
  }
  .container--central {
    padding-left: 40px;
  }
  .container--footer {
    padding-right: 39px;
  }
}
@media (max-width: 767px) {
  .container {
    min-width: 320px;
    padding-left: 15px;
    padding-right: 15px;
  }
  .container--central {
    padding-left: 13px;
    padding-right: 10px;
  }
  .container--gym,
.container--strengths {
    padding-left: 0;
    padding-right: 0;
  }
  .container--footer {
    padding-left: 15px;
    padding-right: 15px;
  }
}
.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ed0233;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.page body {
  height: 100%;
  font-family: "Roboto Condensed", sans-serif, "Arial";
  font-size: 16px;
  line-height: 24px;
  color: #102654;
  background-color: #ffffff;
  position: relative;
}

.page header {
  margin: 0;
}

.page .wrapper {
  min-height: 100vh;
}

.page footer {
  margin-top: auto;
  margin-bottom: 0;
}

@media (max-width: 767px) {
  .page body {
    min-width: 320px;
  }
}
.main-header {
  width: 100%;
  padding-top: 30px;
  padding-bottom: 30px;
  position: absolute;
  top: 0;
  left: 0;
  color: #ffffff;
  font-family: "Alumni Sans", sans-serif, "Arial";
  font-size: 26px;
  font-weight: 600;
  line-height: 31.2px;
}

.main-header__navigation {
  margin: 0 auto;
}

@media (max-width: 767px) {
  .main-header {
    padding: 15px 0;
  }
}
.header-navigation {
  max-width: 1216px;
  display: flex;
  justify-content: space-between;
}

.header-navigation a {
  color: #ffffff;
  font-size: 26px;
  line-height: 31.2px;
  letter-spacing: 0.03em;
  text-decoration: none;
}

.header-navigation__logo-wrap {
  padding-top: 10px;
}

@media (max-width: 1199px) {
  .header-navigation a {
    padding-top: 5px;
    font-size: 24px;
    line-height: 28.8px;
    letter-spacing: 0.02em;
  }
}
@media (max-width: 767px) {
  .header-navigation a {
    padding-top: 0;
    font-size: 20px;
    line-height: 24px;
  }
}
.main-content__contacts-form-wrap {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
}

.main-content__form {
  grid-column: 1/2;
}

.main-content__contacts {
  grid-column: 2/3;
}

@media (max-width: 1199px) {
  .main-content__contacts-form-wrap {
    display: flex;
    flex-direction: column;
  }
}
.main-footer {
  width: 100%;
  color: #ffffff;
  background-color: #030a1a;
}

.main-footer__wrap {
  margin: 0 auto;
  padding-top: 44px;
  padding-bottom: 20px;
  display: flex;
  justify-content: space-between;
  gap: 15px;
}

@media (max-width: 1199px) {
  .main-footer__wrap {
    padding-top: 45px;
    padding-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .main-footer__wrap {
    padding: 45px 15px;
    flex-direction: column;
    position: relative;
  }
  .main-footer__navigation {
    margin-bottom: 19px;
  }
}
.footer-navigation__list {
  min-width: 440px;
  margin: 0;
  padding: 0;
  padding-left: 10px;
  color: #ffffff;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 30px;
  list-style-type: none;
}

.footer-navigation__item a {
  font-family: "Roboto Condensed", sans-serif, "Arial";
  font-weight: 400;
  font-size: 14px;
  line-height: 16.41px;
  color: #ffffff;
}

.footer-navigation__item a:hover {
  color: #2e35ac;
}

@media (max-width: 1199px) {
  .footer-navigation__list {
    width: min-content;
    min-width: 391px;
    margin: 0 auto;
    padding-left: 3px;
    padding-right: 0;
    gap: 26px;
  }
}
@media (max-width: 767px) {
  .footer-navigation__list {
    width: 100%;
    min-width: 290px;
    padding-left: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    row-gap: 16px;
    column-gap: 30px;
  }
  .footer-navigation__list-item {
    min-width: 120px;
  }
  .footer-navigation__list li:first-child {
    grid-column: 1/2;
    grid-row: 1/3;
    align-self: end;
  }
  .footer-navigation__item--pass {
    grid-column: 1/2;
    grid-row: 3/4;
  }
  .footer-navigation__item-promo {
    grid-column: 2/3;
  }
  .footer-navigation__item-coach {
    grid-column: 2/3;
  }
  .footer-navigation__item-timetable {
    grid-column: 2/3;
  }
  .footer-navigation__item a {
    font-size: 16px;
    line-height: 18.75px;
  }
}
