  margin-bottom: 37px;
  padding-left: 4px;
  font-family: "Alumni Sans", sans-serif, "Arial";
  font-size: 54px;
  line-height: 64.8px;
  text-align: center;
}

.reviews__list-buttons-wrap {
  width: 58.93%;
  max-width: 672px;
  padding-left: 56px;
  padding-right: 56px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.reviews__prev-button {
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  position: absolute;
}

.reviews__next-button {
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  position: absolute;
}

.reviews__container {
  width: 100%;
  max-width: 560px;
  min-height: 242px;
  margin-top: 0 auto;
  padding: 0;
  list-style-type: none;
  overflow: hidden;
  height: 100%;
}

.reviews__inner-container {
  display: flex;
}

.reviews__item {
  display: block;
  flex-shrink: 0;
}

.reviews__item--active {
  display: block;
  width: 100%;
  margin-bottom: 30px;
  padding: 56px 30px;
  color: #ffffff;
  background-color: #1c3374;
  box-shadow: 0 0 40 0 rgba(28, 51, 116, 0.2);
}

.reviews__item-wrap {
  width: 100%;
  max-width: 436px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 80px 1fr;
  grid-template-rows: min-content 1fr;
  column-gap: 25px;
  row-gap: 10px;
}

.reviews__item img,
.reviews__item picture {
  width: 80px;
  height: 80px;
  margin: 0;
  margin-top: 4px;
  padding: 0;
  display: block;
  overflow: hidden;
  grid-column: 1/2;
  grid-row: 1/3;
  border-radius: 50%;
  align-self: top;
}

.reviews__item h3 {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 3px;
  font-weight: 700;
  font-size: 20px;
  line-height: 30px;
  color: #ffffff;
  text-align: left;
  grid-column: 2/3;
  grid-row: 1/2;
}

.reviews__item blockquote {
  margin: 0;
  margin-left: 3px;
  padding: 0;
  font-size: 16px;
  line-height: 22.4px;
  text-align: left;
  grid-column: 2/3;
  grid-row: 2/3;
}

@media (max-width: 1199px) {
  .reviews {
    padding-bottom: 85px;
  }
  .reviews h2 {
    margin-top: 0;
    margin-bottom: 40px;
    padding-right: 3px;
    font-size: 42px;
    line-height: 50.4px;
  }
  .reviews__list-buttons-wrap {
    width: 100%;
    max-width: 100%;
  }
  .reviews__item--active {
    padding: 56px 62px;
  }
  .reviews__container {
    max-width: 100%;
  }
  .reviews__item-wrap {
    max-width: 100%;
  }
}
@media (max-width: 767px) {
  .reviews {
    padding-bottom: 40px;
  }
  .reviews h2 {
    margin-bottom: 42px;
    font-size: 32px;
    line-height: 38.4px;
    grid-row: 1/2;
    grid-column: 2/3;
  }
  .reviews__list-buttons-wrap {
    min-width: 290px;
    padding-left: 32px;
    padding-right: 32px;
  }
  .reviews__item-wrap {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: min-content 1fr;
    column-gap: 4px;
    row-gap: 17px;
  }
  .reviews__item img,
.reviews__item picture {
    width: 68px;
    height: 68px;
    margin-top: 0;
    margin-left: 0;
    grid-row: 1/2;
    grid-column: 1/2;
    justify-self: end;
  }
  .reviews__item {
    min-width: 226px;
    padding: 40px 25px 31px 25px;
  }
  .reviews__item blockquote {
    text-align: center;
  }
  .reviews__item h3 {
    margin-top: 9px;
    margin-left: 4px;
    align-self: flex-start;
    line-height: 26px;
  }
  .reviews__item blockquote {
    grid-row: 2/3;
    grid-column: 1/3;
  }
}
.trial-training {
