}

.trial-training h2 {
  margin-top: 0;
  margin-bottom: 15px;
  font-family: "Alumni Sans", sans-serif, "Arial";
  font-weight: 800;
  font-size: 54px;
  line-height: 84.24px;
  color: #ffffff;
  text-align: left;
}

.trial-training__form {
  margin: 0 auto;
}

@media (max-width: 1199px) {
  .trial-training {
