    font-size: 42px;
    line-height: 50.4px;
  }
  .trial-training__outer-wrap {
    width: 77.08%;
    max-width: 854px;
    padding-left: 45px;
    padding-right: 45px;
    margin: 0 auto;
  }
  .trial-training__inner-wrap {
    width: 94.02%;
    max-width: 718px;
  }
  .trial-training__form {
    margin-left: 0;
    margin-right: auto;
  }
}
@media (max-width: 767px) {
  .trial-training__outer-wrap,
.trial-training__inner-wrap {
    width: 100%;
    max-width: 100%;
  }
  .trial-training__outer-wrap {
    padding-left: 15px;
    padding-right: 15px;
  }
  .trial-training h2 {
    margin-bottom: 35px;
    font-weight: 600;
    font-size: 32px;
    line-height: 38.4px;
  }
}
.contacts {
  width: 100%;
  padding-top: 56px;
  padding-bottom: 48px;
  color: #102654;
  border-top: 1px solid #74819c;
}

.contacts__outer-wrap {
  width: 100%;
  max-width: 555px;
  padding-left: 25px;
  padding-right: 25px;
  margin-left: 0;
  margin-right: auto;
}

.contacts__inner-wrap {
  width: 100%;
  max-width: 410px;
  margin-left: auto;
  margin-right: 0;
}

.contacts h2 {
  margin-top: 0;
  margin-bottom: 37px;
  font-family: "Alumni Sans", sans-serif, "Arial";
  font-weight: 600;
  font-size: 54px;
  line-height: 64.8px;
  text-align: left;
  color: #102654;
}

.contacts__wrap {
  width: 100%;
  max-width: 404px;
}

.contacts__list {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 57% 43%;
  grid-template-rows: 1fr 1fr;
  position: relative;
}

.contacts__list:after {
  content: "";
  width: 1px;
  height: 80%;
  background-color: #74819c;
  position: absolute;
  top: 5%;
  left: 57%;
}

.contacts__list li {
  width: 100%;
  display: flex;
}

.contacts__list-item-wrap {
  width: 100%;
}

.contacts__list-item {
  height: min-content;
  padding-left: 4px;
  padding-bottom: 14px;
}

.contacts__list li:first-child,
.contacts__list li:nth-child(2n+1) {
  margin-left: 0;
  margin-right: auto;
}

.contacts__list li:nth-child(2n) .contacts__list-item-wrap {
  width: 68%;
  max-width: 169px;
  margin-left: auto;
  margin-right: 0;
  padding-left: 0;
}

.contacts__list-item h3 {
  margin-top: 0;
  margin-bottom: 3px;
  font-family: "Roboto Condensed", sans-serif, "Arial";
  font-size: 16px;
  line-height: 24px;
  color: #102654;
}

.contacts__list-item span,
.contacts__list-item a {
  font-weight: 400;
  font-size: 14px;
  line-height: 21.33px;
  color: #102654;
}

@media (max-width: 1199px) {
  .contacts {
    padding-top: 62px;
    padding-bottom: 42px;
  }
  .contacts h2 {
    margin-bottom: 32px;
    font-size: 42px;
    line-height: 50.4px;
  }
  .contacts__outer-wrap {
    width: 77.08%;
    max-width: 854px;
    margin: 0 auto;
    padding-left: 45px;
    padding-right: 45px;
  }
  .contacts__inner-wrap {
    width: 94.02%;
    max-width: 718px;
    margin-left: 0;
    margin-right: auto;
  }
  .contacts__list-item {
    padding-left: 0;
    padding-bottom: 20px;
  }
  .contacts__list-item h3 {
    margin-top: 0;
    margin-bottom: 5px;
    font-size: 20px;
    line-height: 30px;
  }
  .contacts__list-item span,
.contacts__list-item a {
    font-size: 18px;
    line-height: 27.42px;
  }
  .contacts__list:after {
    left: 56.5%;
  }
  .contacts__list li:nth-child(2n) .contacts__list-item-wrap {
    width: 77%;
    max-width: 77%;
  }
}
@media (max-width: 767px) {
  .contacts h2 {
    margin-bottom: 36px;
    font-size: 32px;
    line-height: 38.4px;
  }
  .contacts__outer-wrap,
.contacts__inner-wrap {
    width: 100%;
    max-width: 100%;
  }
  .contacts__outer-wrap {
    padding-left: 15px;
    padding-right: 15px;
  }
  .contacts__list {
    display: flex;
    flex-direction: column;
  }
  .contacts__inner-wrap,
.contacts__list li:nth-child(2n) .contacts__list-item-wrap {
    width: 100%;
    max-width: 100%;
  }
  .contacts__list:after {
    display: none;
  }
  .contacts__list-item--phone {
    order: 2;
  }
  .contacts__list-item--mail {
    order: 3;
  }
}
.form {
  width: 100%;
  max-width: 410px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: min-content 1fr;
  gap: 25px;
}

.form__name-field {
  grid-row: 1/2;
  grid-column: 1/2;
}

.form__telephone-field {
  grid-row: 1/2;
  grid-column: 2/3;
}

.form__submit-wrap {
  grid-row: 2/3;
  grid-column: 1/3;
}

.form__submit-wrap button {
  width: 100%;
  margin-top: 0;
  margin-bottom: auto;
  padding: 18px;
  font-family: "Roboto Condensed", sans-serif, "Arial";
  font-weight: 700;
  font-size: 14px;
  line-height: 16.41px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  border: 3px solid #ffffff;
  background-color: transparent;
}

.form__submit-wrap button:hover {
  color: #102654;
  border-color: #102654;
  background-color: #ffffff;
}

@media (max-width: 1199px) {
  .form {
    max-width: 100%;
    column-gap: 56px;
    row-gap: 28px;
  }
  .form__submit-wrap button {
    padding-bottom: 15px;
  }
}
@media (max-width: 767px) {
  .form {
    max-width: 100%;
    display: flex;
    flex-direction: column;
  }
  .form__submit-wrap button {
    margin-top: 10px;
  }
}
