  padding: 0;
  display: flex;
}

.logo-block__link svg {
  width: 122px;
  height: 44px;
  display: block;
}

.logo-block--footer svg {
  width: 88px;
  height: 32px;
}

.logo-block--footer .logo-block__link {
  width: min-content;
}

@media (max-width: 1199px) {
