.slider-button {
  width: 16px;
  height: 32px;
}

.slider-button svg {
  width: 16px;
  height: 32px;
  fill: none;
  stroke: #afafaf;
  stroke-width: 1px;
}

.slider-button--prev svg {
  transform: rotate(180deg);
}

.slider-button:hover svg,
.slider-button:active svg,
.slider-button:focus svg {
  stroke: #1c3374;
}

@media (max-width: 767px) {
  .slider-button,
.slider-button svg {
    width: 10px;
    height: 19px;
  }
}
.input-wrap input {
  width: 100%;
  height: 31px;
  padding-top: 10px;
  padding-bottom: 10px;
  font-family: "Roboto Condensed", sans-serif, "Arial";
  font-weight: 400;
  font-size: 14px;
  line-height: 16.41px;
  color: #ffffff;
  border-width: 0;
  border-bottom: 1px solid #74819c;
  background-color: transparent;
  cursor: text;
}

.input-wrap input::placeholder {
  font-family: "Roboto Condensed", sans-serif, "Arial";
  font-weight: 400;
  font-size: 14px;
  line-height: 16.41px;
  color: #ffffff;
}

.input-wrap input:hover {
  border-bottom: 2px solid #e8e8e8;
}

.input-wrap input:focus-visible {
  outline: none;
}

.input-wrap input.valid,
.input-wrap input.valid:hover,
.input-wrap input.valid:active {
  border-bottom: 2px solid #003eb7;
}

.input-wrap input.invalid {
  border-bottom: 2px solid #ed0233;
}

